.home {
  background-image: linear-gradient(
      35deg,
      rgba(204, 179, 95, 0.5),
      rgba(54, 102, 175, 0.5)
    ),
    url(../../assets/bgimg5.jpg);;
  background-size: cover;
  width: 100%;
  height: 100vh;
}
/* rgba(253, 158, 253, 0.5), */
/* rgba(136, 128, 248, 0.5) */
.home__container {
  row-gap: 7rem;
}

.home__content {
  grid-template-columns: 116px 1.3fr 1fr;
  padding-top: 5.5fr;
  column-gap: 2rem;
  align-items: center;
}

.home__social {
  display: grid;
  grid-template-columns: max-content;
  row-gap: 1rem;
}
.home__social-icon {
  font-size: 1.25rem;
  color: var(--title-color);
}

.home__social-icon:hover {
  color: var(--title-color-dark);
}
.home__title {
  font-size: var(--big-font-size);
  margin-bottom: var(--mb-0-25);
  font-weight: 700;
}

.home__hand {
  width: 38px;
  height: 38px;
  margin-left: 0.4rem;
}

.home__subtitle {
  position: relative;
  font-size: var(--h3-font-size);
  /* padding-left: 5rem; */
  font-weight: var(--font-normal);
  margin-bottom: var(--mb-1);
}
/* .home__subtitle::before{
content: "";
position: absolute;
width: 70px;
height: 1px;
background-color: var(--text-color);
left: 0;
top: 1rem;
} */

.home__description {
  max-width: 400px;
  margin-bottom: var(--mb-3);
}

.home__img {
  background: url(../../assets///profile.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: inset 0 0 0 9px rgb(255 255 255/30%);
  order: 1;
  justify-self: center;
  width: 300px;
  height: 300px;
  animation: profile_animate 8s ease-in-out infinite 1s;
}

@keyframes profile_animate {
  0% {
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%;
  }
  100% {
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  }
}

.home__scroll {
  margin-left: 9.25rem;
}

.wheel {
  animation: scroll 2s ease infinite;
}
@keyframes scroll {
  0% {
    transform: translateY(0);
  }
  30% {
    transform: translateY(3.75rem);
  }
}

.home__scroll-name {
  color: var(--title-color);
  font-weight: var(--font-medium);
  margin-right: var(--mb-0-25);
  margin-left: 2px;
}

.home__scroll-arrow {
  font-size: 1.25rem;
  color: var(--title-color);
}
@media screen and (max-width: 992px) {
  .home__content {
    grid-template-columns: 100px 1.3fr 1fr;
    column-gap: 1.25fr;
  }
  .home__hand {
    width: 26px;
    height: 26px;
  }
  .home__subtitle {
    /* padding-left: 3.75rem; */
    margin-bottom: var(--mb-1);
  }
  /* .home__subtitle::before{
    width: 42px;
    top:0.8rem;
} */
  .home__description {
    max-width: initial;
    margin-bottom: var(--mb-2-5);
  }
  .home__img {
    width: 250px;
    height: 250px;
    box-shadow: inset 0 0 0 9px rgb(255 255 255/30%);
  }
  .home__scroll {
    margin-left: 7.5rem;
  }
}

/* For medium devices */
@media screen and (max-width: 768px) {
  .home__content {
    grid-template-columns: 0.5fr 3fr;
    padding-top: 3.5rem;
  }
  .home__img {
    order: initial;
    justify-self: initial;
  }
  .home__data {
    grid-column: 1/3;
  }
  .home__img {
    box-shadow: inset 0 0 0 9px rgb(255 255 255/30%);
    width: 200px;
    height: 200px;
  }
  .home__scroll {
    display: none;
  }
}

/* @media screen and (max-width: 576px) {
    .home__container
  } */

/* For small devices */
@media screen and (max-width: 450px) {
  .home__img {
    width: 180px;
    height: 180px;
  }
  .home__hand {
    width: 22px;
    height: 22px;
  }
}

#madebyvinay {
  font-size: 30px;
  color: rgb(233, 69, 96);
  font-weight: 700;
  width: 0ch;
  overflow: hidden;
  white-space: nowrap;
  animation: tagline 10s infinite alternate;
  border-right: 3px solid rgb(233, 69, 96);
}

@keyframes tagline {
  0% {
    width: 0ch;
  }
  50% {
    width: 21.5ch;
  }
}
